<template>
	<div>
		<van-nav-bar class="hader" title="动态详情" left-text="返回" left-arrow @click-left="back()">
		  <a href="https://m.guanfan.top/#/app" slot="right" v-if="isiOS!=true&&isWeixin!=true">
		    <van-button type="primary" size="small">安装APP</van-button>
		  </a>
		  <a href="https://apps.apple.com/cn/app/id1487127641" slot="right" v-if="isiOS==true&&isWeixin!=true">
		    <van-button type="primary" size="small">安装APP</van-button>
		  </a>
		  <a href="https://mp.weixin.qq.com/mp/profile_ext?action=home&__biz=MzU0NTg5ODQzNA==&scene=124#wechat_redirect" slot="right" v-if="isWeixin==true">
		    <van-button type="primary" size="small">关注</van-button>
		  </a>
		</van-nav-bar>
		<video width="375" height="300" controls="controls" class="mt50" v-if="themeDetail.isvideo">
		  <source :src="themeDetail.videourl" type="video/mp4" />
		  <object :data="themeDetail.videourl" width="375" height="300">
		    <embed :src="themeDetail.videourl" width="375" height="300" />
		  </object>
		</video>
		<van-swipe :autoplay="3000" class="food-img mt50" v-else>
		  <van-swipe-item v-for="(image, index) in swiperList" :key="index">
			<div class="food-img" :style="{backgroundImage:'url('+osshost+ image +detailpic+')'}"></div>
		  </van-swipe-item>
		</van-swipe>
		<div class="cont mt10" style="margin-top: 0.5rem;">
		  <!--左边区域-->
		    <!--封面图-->
		    
		    <!--用户信息-->
		    <div>
		      <ul class="dr_list">
		        <li class="clearfix mb15">
		          <div class="left headicon bg-img br50"
		               :style="{backgroundImage:'url('+imgurl+')'}" v-if="!themeDetail.faceimg">
		          </div>
				  <div class="left headicon bg-img br50"
				       :style="{backgroundImage:'url('+osshost+ themeDetail.faceimg +userface+')'}" v-if="isThree==false">
				  </div>
				  <div class="left headicon bg-img br50"
				       :style="{backgroundImage:'url('+themeDetail.faceimg +')'}" v-if="isThree==true">
				  </div>
		          <div class="info">
		            <h2 class="text-bold text-df text-16">{{themeDetail.nickname}}</h2>
					<span class="grayColor text-14">{{themeDetail.pushtime}}</span>
		          </div>
				  <i class="icon-like van-icon van-icon-good-job text-right right mt10" :class="themeDetail.isliked==1?'text-red':''"  @click="changeLike()">{{ themeDetail.like_num }}</i>
		        </li>
		      </ul>
		      <div class="text-df">{{themeDetail.description}}</div>
			  <div class="dt-dz">
			  	<div class="dt-dz-l">{{themeDetail.like_num}}人点赞</div>
			  	<div class="dt-dz-r">
			  		<span v-for="(item,index) in themeDetail.like_people_list" :key="index" :style="{backgroundImage:'url('+imgurl+')'}" v-if="!item.faceimg"></span>
					<span v-for="(item,index) in themeDetail.like_people_list" :key="index" :style="{backgroundImage:'url('+item.faceimg+')'}" v-if="item.faceimg&&item.isThree"></span>
					<span v-for="(item,index) in themeDetail.like_people_list" :key="index" :style="{backgroundImage:'url('+osshost+ item.faceimg +userface+')'}" v-if="!item.isThree&&item.faceimg"></span>
			  	</div>
			  </div>
			  <a href="https://m.guanfan.top/#/app" slot="right" v-if="isiOS!=true&&isWeixin!=true">
			    <van-button class="wb100 mt10 mb10" type="primary">去APP发布动态</van-button>
			  </a>
			  <a href="https://apps.apple.com/cn/app/id1487127641" slot="right" v-if="isiOS==true&&isWeixin!=true">
			    <van-button class="wb100 mt10 mb10" type="primary">去APP发布动态</van-button>
			  </a>
			    <div v-if="isWeixin==true" class="gzh-guanzhu">
			  	  <img src="../assets/img/qr.jpg"/>
			  	  <span>长按识别二维码，关注管饭美食</span>
			    </div>
		    </div>
		    <!--  推荐菜谱    -->
		    <div class="mt20" v-if="relation_item !=null">
		      <h2>关联菜谱</h2>
			  <van-card
			    :desc="relation_item.stuff"  
			    :title="relation_item.title"
			    :thumb="osshost+ relation_item.cpimg +listpic"
				 @click="bookDetail(relation_item.id)"
			  />
		    </div>
			
			
		
		
		</div>
	</div>
</template>

<script>
	import qs from 'qs';
	import axios from 'axios';
	export default {
		name: "cookbookinfo",
		data() {
		  return {
		    num: 13,
		    swiperList: [],
		    themeDetail: '',
			relation_item:'',
		    osshost: 'http://guanfan.oss-cn-hangzhou.aliyuncs.com/',
		    // osshost: 'http://guanfan.oss-cn-hangzhou.aliyuncs.com/',
		    listpic: '?x-oss-process=style/list-img',
		    userface: '?x-oss-process=style/user-img',
		    detailpic: '?x-oss-process=style/detail-img',
			isWeixin:'',
			isiOS:'',
			token:'',
			imgurl: '../assets/img/userIcon.png',
			isThree: false
		  }
		},
		created() {
		  this.did=this.$route.query.id;
		  this.token = this.$store.state.user.token || '';
		  this.getDataInfo(this.did);
		  this.getwx();
		  this.appSource();
		},
		methods: {
			getwx(){
						var ua = window.navigator.userAgent.toLowerCase(); 
						  if (ua.match(/MicroMessenger/i) == 'micromessenger') {
						    this.isWeixin=true;
						  } else {
						   this.isWeixin=false;
						  } 
			},
			appSource() {
						const u = navigator.userAgent, app = navigator.appVersion;
						if (!!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)) {
							this.isiOS=true;
						} else {
							this.isiOS=false;
						}
					},
		  // getDataInfo(did) {
			 //  getActionDetail(did).then(res=>{
			 //  				  if(res.data.success){
			 //  					this.themeDetail = res.data.result;
			 //  					this.avatar =this.themeDetail.like_people_list;
			 //  					this.relation_item =this.themeDetail.relation_item;
			 //  					this.swiperList=this.themeDetail.dtimg.split('|');
			 //  					this.relation_item.stuff=this.relation_item.stuff.split('|');
			 //  					for(var i=0;i<this.relation_item.stuff.length;i++){
			 //  						let array=[];
			 //  						array=this.relation_item.stuff[i].split('#');
			 //  						this.relation_item.stuff[i]=array[0]
			 //  					}
			 //  				  }
			 //  }).catch(error=>{
			 //  				  console.log(error)
			 //  })
		  // },
		  getDataInfo(did) {
		  	let data = qs.stringify({
		  		did: did
		  	});
		  	axios({
		  		method: 'post',
		  		url: '/detail/dongtai',
		  		headers: {
		  			'Content-Type': 'application/x-www-form-urlencoded',
					'token':this.token
		  		},
		  		data
		  	}).then(response => {
		  		if (response.data.success) {
		  			this.themeDetail = response.data.result;
					let str = new RegExp('http');
					this.isThree = str.test(this.themeDetail.faceimg);
		  			this.avatar =this.themeDetail.like_people_list;
					for(var i=0;i<this.avatar.length;i++){
						this.avatar[i].isThree = new RegExp('http').test(this.avatar[i].faceimg);
					};
		  			this.relation_item =this.themeDetail.relation_item;
		  			this.swiperList=this.themeDetail.dtimg.split('|');
		  			this.relation_item.stuff=this.relation_item.stuff.split('|');
		  			for(var i=0;i<this.relation_item.stuff.length;i++){
		  				let array=[];
		  				array=this.relation_item.stuff[i].split('#');
		  				this.relation_item.stuff[i]=array[0]
		  			}
		  		}
		  	});
		  },
		  changeLike(){
		  	if(this.token==""){
		  			this.$router.push({ path: '/pswLogin'});
		  	}else{
		  		if(this.themeDetail.isliked==1){
					let data = qs.stringify({
						itemid:this.did,
						itemtype:1
					});
					axios({
						method: 'post',
						url: '/action/unlike',
						headers: {
							'Content-Type': 'application/x-www-form-urlencoded',
							'token':this.token
						},
						data
					}).then(response => {
						if (response.data.success) {
							this.themeDetail.isliked=0;
							this.themeDetail.like_num--
						}
					});
		  			// setUnLike(this.did,1).then(res=>{
		  			// 	if(res.data.success){
		  			// 		this.themeDetail.isliked=0;
		  			// 	    this.themeDetail.like_num--
		  			// 	}
		  			// })
		  		}else{
					let data = qs.stringify({
						itemid:this.did,
						itemtype:1
					});
					axios({
						method: 'post',
						url: '/action/like',
						headers: {
							'Content-Type': 'application/x-www-form-urlencoded',
							'token':this.token
						},
						data
					}).then(response => {
						if (response.data.success) {
							this.themeDetail.isliked=1;
							this.themeDetail.like_num++
						}
					});
		  			// setLike(this.did,1).then(res=>{
		  			// 	if(res.data.success){
		  			// 		this.themeDetail.isliked=1;
		  			// 	    this.themeDetail.like_num++
		  			// 	}
		  			// })
		  		}
		  	}
		  },
		back(){
		        this.$router.go(-1);//返回上一层
		    },
			bookDetail(id) {
			  this.$router.push({path: "/cookbookinfo",query:{id:id}})
			},
		}
	}
</script>

<style>
.food-img img{
	width: 100%;
}
.dt-dz{
	width: 100%;
	height: 2rem;
	margin-top: 1rem;
}
.dt-dz-l{
	width: 5rem;
	line-height: 2rem;
	font-size: 0.8rem;
	color: #333;
	text-align: left;
	float: left;
}
.dt-dz-r{
	float: right;
}
.dt-dz-r span{
	width: 2rem;
	height: 2rem;
	display: block;
	float: left;
	background-position: center;
	background-size: 100%;
	border-radius: 100%;
	border: 1px solid #eee;
	    margin-left: -0.5rem;
}
</style>
